<template>
  <div class="user">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/carnival/users' }"></el-breadcrumb-item>
    </el-breadcrumb>

    <div class="user-operate">
      <el-form :inline="true">
        <el-form-item label>
          <el-select size="small" v-model="param.deptId">
            <el-option
              v-if="item.deptName"
              v-for="(item,index) in depts"
              :key="index"
              :value="item.deptId"
              :label="item.deptName"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label>
          <el-input placeholder="" v-model="param.name" size="small"></el-input>
        </el-form-item>

        <el-form-item label>
          <el-button type="warning" size="small" @click="search()"></el-button>
        </el-form-item>

        <div class="user-operate-right">
          <el-button type="info" size="small" @click="isImport = true;fileList=[]"></el-button>

          <el-button type="info" size="small" @click="add()"></el-button>
        </div>
      </el-form>
    </div>

    <div class="user-list">
      <el-table
        ref="singleTable"
        :data="tableData"
        highlight-current-row
        style="width: 100%;min-height:380px;"
      >
        <el-table-column type="index" label="" width="80"></el-table-column>

        <el-table-column property="userName" label="" width="220"></el-table-column>

        <el-table-column property="deptName" label="" width="120"></el-table-column>

        <el-table-column property="aliveAmount" label="" width="120"></el-table-column>

        <el-table-column property="rechargeAmount" label="" width="120"></el-table-column>

        <el-table-column property="balance" label="" width="120"></el-table-column>

        <el-table-column property label="">
          <template slot-scope="scope">
            <div>
              <span @click="setValue(scope.row)" class="user-list-value"></span>
              
              <span @click="edit(scope.row)" class="user-list-edit"></span>
              
              <span @click="resetPass(scope.row)" class="user-list-reset"></span>
              
              <!-- <span @click="incomeAndPay(scope.row)" class="red"></span> -->
              
              <span @click="del(scope.row)" class="user-list-del"></span>
            </div>
          </template>
        </el-table-column>
      </el-table>

      <el-pagination
        @current-change="handleCurrentChange"
        :current-page="param.current"
        :page-size="param.size"
        layout="total, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </div>
    <el-dialog
      custom-class="add-user"
      :title="isEditOrAdd=='add'?'':''"
      :visible.sync="isAddUser"
      width="350px"
    >
      <el-form ref="userform" :rules="rules" :model="userParam" label-width="80px">
        <el-form-item label="" prop="name">
          <el-input size="small" v-model="userParam.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="deptId">
          <el-select size="small" v-model="userParam.deptId">
            <el-option 
              v-if="item.deptName"
              v-for="(item,index) in depts.slice(1)"
              :key="index"
              :value="item.deptId"
              :label="item.deptName"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="isAddUser = false"> </el-button>
        <el-button size="small" type="primary" @click="addUser('userform')"> </el-button>
      </span>
    </el-dialog>

    <el-dialog custom-class="set-value" title="" :visible.sync="isSetValue" width="350px">
      <el-form ref="valueform" :rules="valuerules" :model="valueParam" label-width="80px">
        <el-form-item label="" prop="amount">
          <el-input size="small" v-model.number="valueParam.amount"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="isSetValue = false"> </el-button>
        <el-button size="small" type="primary" @click="saveValue('valueform')"> </el-button>
      </span>
    </el-dialog>

    <el-dialog custom-class="importUser" title="" :visible.sync="isImport" width="350px">
      <el-form ref="importform" :model="valueParam" label-width="80px">
        <el-form-item label prop>
          <el-upload
            class="upload-demo"
            drag
            action="/user/import"
            :before-upload="beforeUpload"
            ref="userupload"
            :limit="1"
            enctype="multipart/form-data"
            :on-error="uploadError"
            :on-success="handleAvatarSuccess"
            :auto-upload="false"
            :file-list="fileList"
          >
            <i class="el-icon-upload"></i>
            <div class="el-upload__text">
              
              <em></em>
            </div>
            <div class="el-upload__tip" slot="tip">xlsx100Mb</div>
          </el-upload>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="isImport = false"> </el-button>
        <el-button size="small" type="primary" @click="importUser('importform')"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
                

<script>
import api from "@/api/api";

export default {
  data() {
    return {
      tableData: [],
      depts: [],
      total: 0,
      pages: 0,
      param: {
        deptId: "",
        name: "",
        current: 1,
        size: 5
      },
      isAddUser: false,
      isEditOrAdd: "add",
      isSetValue: false,
      isImport: false,
      fileList: [],
      file: "",
      userParam: {
        name: "",
        deptId: ""
      },
      valueParam: {
        amount: ""
      },
      curUserId: "",
      rules: {
        name: {
          type: "string",
          required: true,
          message: ""
        },
        deptId: {
          type: "string",
          required: true,
          message: ""
        }
      },
      valuerules: {
        amount: {
          type: "number",
          required: true,
          min: 1,
          message: "0"
        }
      }
    };
  },
  created() {
    this.getDepts();

    this.getUserList();
  },
  methods: {
    incomeAndPay() {
        this.$router.push("/carnival/wealthDetail");
    },
    importUser() {
      this.$refs.userupload.submit();
    },
    uploadError() {
      this.$message.error("");
    },
    handleAvatarSuccess(res, file) {
      this.isImport = false;
      if (res.code == "200") {
        this.getUserList();
        this.$message.success("");
      } else {
        this.$message.error(res.message || "");
      }
    },
    beforeUpload(file) {
      this.file = file;
      const extension = file.name.split(".")[1] === "xlsx";
      const isLt2M = file.size / 1024 / 1024 < 100;
      if (!extension) {
        this.$message.warning("xlsx!");
        return false;
      }
      if (!isLt2M) {
        this.$message.warning(" 100MB!");
        return false;
      }
    },
    getDepts() {
      api.depts().then(res => {
        if (res.code == "200") {
          this.depts = res.data;

          this.depts.unshift({
            deptId: "",

            deptName: ""
          });
        }
      });
    },
    getUserList() {
      api.userList(this.param).then(res => {
        if (res.code == 200) {
          this.tableData = res.data.records;

          this.param.size = res.data.size;

          this.total = parseInt(res.data.total);

          this.page = parseInt(res.data.pages);
        } else {
          this.$message.error(res.message || "");
        }
      });
    },
    handleCurrentChange(current) {
      this.param.current = current;
      this.getUserList();
    },
    search() {
      this.param.current = 1;
      this.getUserList();
    },
    add() {
      this.isAddUser = true;
      this.isEditOrAdd = "add";
      this.userParam = {
        name: "",
        id: "",
        deptId: ""
      };
    },
    addUser(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          api.addUser(this.userParam).then(res => {
            if (res.code == "200") {
              this.$message.success("");

              this.isAddUser = false;

              //
              this.param.current = 1;
              this.getUserList();
            } else {
              this.$message.error(res.message || "");
            }
          });
        } else {
          return false;
        }
      });
    },
    del(item) {
      item = JSON.parse(JSON.stringify(item));
      this.$confirm("", "").then(() => {
        api
          .delUser({
            userId: item.userId
          })
          .then(res => {
            if (res.code == 200) {
              this.$message.success("");

              //
              this.param.current = 1;
              this.getUserList();
            } else {
              this.$message.error(res.message || "");
            }
          });
      });
    },
    resetPass(item) {
      item = JSON.parse(JSON.stringify(item));
      this.$confirm(" 888888", "", {
        type: "warning"
      }).then(() => {
        api
          .resetPass({
            userId: item.userId
          })
          .then(res => {
            if (res.code == 200) {
              this.$message.success("");

              //

              this.getUserList();
            } else {
              this.$message.error(res.message || "");
            }
          });
      });
    },
    setValue(item) {
      item = JSON.parse(JSON.stringify(item));
      this.isSetValue = true;
      this.valueParam.amount = "";
      this.curUserId = item.userId;
    },
    saveValue(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let params = {
            amount: this.valueParam.amount,

            userId: this.curUserId
          };

          api.recharge(params).then(res => {
            if (res.code == 200) {
              this.$message.success("");

              this.isSetValue = false;

              //

              this.getUserList();
            } else {
              this.$message.error(res.message || "");
            }
          });
        } else {
          return false;
        }
      });
    },
    edit(item) {
      item = JSON.parse(JSON.stringify(item));
      this.userParam = {
        name: item.userName,

        id: item.userId,

        deptId: item.deptId
      };

      this.isAddUser = true;

      //

      this.isEditOrAdd = "edit";
    }
  }
};
</script>

<style lang="less">
.user {
  &-operate {
    .el-form {
      display: flex;
      align-items: center;
      &-item {
        width: 120px;
        margin-bottom: 10px;
        margin-top: 10px;
      }
    }
    &-right {
      margin-left: auto;
    }
  }

  &-list {
    &-value {
      color: #74cd88;
      font-size: 14px;
      cursor: pointer;
      padding-right:5px;
    }
    &-del {
      color: #fb846b;
      font-size: 14px;
      cursor: pointer;
      padding-right:5px;
      padding-left:5px;
    }
    &-edit {
      color: #7796ff;
      font-size: 14px;
      cursor: pointer;
      padding-right:5px;
    }
    &-reset {
      color: #70707d;
      font-size: 14px;
      cursor: pointer;
      padding-right:5px;
    }
  }
}

.add-user {
  .el-input {
    width: 215px;
  }
}

.importUser {
  width: 600px !important;
}
</style>


