<template>
  <div class="selection">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/carnival/program/selection' }"></el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/carnival/program/selection' }"></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="selection-operate">
      <el-form :inline="true">
        <el-form-item label=":">
          <el-date-picker size="small" v-model="startDate" align="right" type="datetime"></el-date-picker>
        </el-form-item>
        <el-form-item label=":">
          <el-date-picker size="small" v-model="endDate" align="right" type="datetime"></el-date-picker>
        </el-form-item>
        <el-form-item label>
          <el-button size="small" type="warning" @click="setScoreDate()"></el-button>
        </el-form-item>
        <div class="selection-operate-right">
          <!-- <el-button type="warning" size="small" @click="projection()"></el-button> -->
          <el-button type="info" size="small" @click="add()"></el-button>
        </div>
      </el-form>
    </div>

    <div>
      <el-table
        ref="singleTable"
        :data="tableData"
        highlight-current-row
        heigth="300px"
        style="width: 100%;"
        :default-sort="{prop: 'orderIndex'}"
      >
        <el-table-column type="index" label="" width="80"></el-table-column>
        <el-table-column property="name" label="" width="120"></el-table-column>
        <el-table-column property="actPerson" label="" width="120"></el-table-column>
        <el-table-column property="orderIndex" label="" width="120" sortable></el-table-column>
        <!-- <el-table-column property="selectionName" label="" width="120">
        </el-table-column>-->
        <el-table-column property="score" label="" width="120" sortable></el-table-column>
        <el-table-column property="appScore" label="app" width="120" sortable></el-table-column>
        <el-table-column property="webScore" label="" width="120" sortable></el-table-column>
        <el-table-column property label="">
          <template slot-scope="scope">
            <div>
              <span @click="edit(scope.row)" class="edit"></span>
              <span @click="vote(scope.row)" class="del"></span>
              <span @click="del(scope.row)" class="red"></span>
            </div>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <el-dialog
      custom-class="add-selection"
      :title="selectionParam.id?'':''"
      :visible.sync="isAddselection"
      width="350px"
    >
      <el-form ref="selectionform" :rules="rules" :model="selectionParam" label-width="80px">
        <el-form-item label="" prop="name">
          <el-input size="small" v-model="selectionParam.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="actUsers">
          <el-input size="small" v-model="selectionParam.actUsers"></el-input>
        </el-form-item>
        <el-form-item label="" prop="orderIndex">
          <el-input size="small" v-model.number="selectionParam.orderIndex"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="isAddselection = false"> </el-button>
        <el-button size="small" type="primary" @click="addselection('selectionform')"> </el-button>
      </span>
    </el-dialog>

    <el-dialog custom-class="vote-selection" title="" :visible.sync="isVote" width="350px">
      <el-form ref="voteform" :rules="voterules" :model="voteParam" label-width="80px">
        <el-form-item label="" prop="name">
          <el-input disabled size="small" v-model="voteParam.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="score">
          <el-input size="small" v-model.number="voteParam.score"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="isVote = false"> </el-button>
        <el-button size="small" type="primary" @click="saveVote('voteform')"></el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import api from "@/api/api";
import unit from "@/config/unit";
export default {
  data() {
    return {
      startDate: "",
      endDate: "",
      tableData: [],
      userTableData: [],
      depts: [],
      total: 11,
      param: {
        deptId: "",
        name: "",
        current: 1,
        size: 8
      },
      isAddselection: false,
      isVote: false,
      selectionParam: {
        name: "",
        actUsers: "",
        orderIndex: "",
        id: ""
      },
      voteParam: {
        id: "",
        name: "",
        score: ""
      },
      rules: {
        name: { type: "string", required: true, message: "" },
        actUsers: { type: "string", required: true, message: "" },
        orderIndex: {
          type: "number",
          min: 1,
          max: 100,
          required: true,
          message: "0~100"
        }
      },
      voterules: {
        score: {
          type: "number",
          min: 1,
          required: true,
          message: "0"
        }
      }
    };
  },
  created() {
    this.getselectionList();
    this.getScoreDate();
  },
  methods: {
    del(item) {
      this.$confirm("", "").then(() => {
        api.delProgram({ id: item.id }).then(res => {
          if (res.code == "200") {
            this.$message.success("");
            this.getselectionList();
          } else {
            this.$message.error("");
          }
        });
      });
    },
    projection() {
      this.$router.push("projection");
    },
    saveVote(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          api.voteselection(this.voteParam).then(res => {
            if (res.code == 200) {
              this.$message.success("");
              this.getselectionList();
              this.isVote = false;
            } else {
              this.$message.error(res.message || "");
            }
          });
        } else {
          return false;
        }
      });
    },
    vote(item) {
      item = JSON.parse(JSON.stringify(item));
      this.voteParam.name = item.name;
      this.voteParam.id = item.id;
      this.voteParam.score = "";
      this.isVote = true;
    },
    add() {
      this.selectionParam = {
        name: "",
        actUsers: "",
        orderIndex: "",
        id: ""
      };
      this.isAddselection = true;
    },
    edit(item) {
      item = JSON.parse(JSON.stringify(item));
      this.isAddselection = true;
      this.selectionParam.name = item.name;
      this.selectionParam.actUsers = item.actPerson;
      this.selectionParam.orderIndex = item.orderIndex;
      this.selectionParam.id = item.id;
    },
    getScoreDate() {
      api.selectionHome().then(res => {
        if (res.code == "200") {
          if (res.data) {
            this.startDate = unit.formatDate(res.data.scoreStartDate);
            this.endDate = unit.formatDate(res.data.scoreEndDate);
          } else {
            this.startDate = "";
            this.endDate = "";
          }
        } else {
          this.$message.error(res.message || "");
        }
      });
    },
    setScoreDate() {
      if (!this.startDate || !this.endDate) {
        this.$message.warning("");
        return;
      }
      if (new Date(this.startDate) >= new Date(this.endDate)) {
        this.$message.warning("");
        return;
      }
      api
        .setScoreDate({
          startDate: unit.formatDate(this.startDate),
          endDate: unit.formatDate(this.endDate)
        })
        .then(res => {
          if (res.code == 200) {
            this.$message.success("");
          } else {
            this.$message.error(res.message || "");
          }
        });
    },
    getselectionList() {
      api.programList().then(res => {
        if (res.code == 200) {
          this.tableData = res.data;
        } else {
          this.$message.error(res.message || "");
        }
      });
    },
    addselection(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          api.addselection(this.selectionParam).then(res => {
            if (res.code == "200") {
              this.$message.success("");
              this.getselectionList();
              this.isAddselection = false;
            } else {
              this.$message.error(res.message || "");
            }
          });
        } else {
          return false;
        }
      });
    }
  }
};
</script>

<style lang="less">
.selection {
  &-operate {
    .el-form {
      display: flex;
      align-items: center;
      &-item {
        margin-bottom: 10px;
        margin-top: 10px;
        .el-input {
          width: 180px;
        }
      }
    }
    &-right {
      padding: 15px 0;
      margin-left: auto;
    }
  }
}

.add-selection {
  .el-col {
    .el-input {
      width: 100%;
    }
  }
  .el-input {
    width: 215px;
  }
  .line {
    height: 32px;
  }
}
</style>


