<template>
  <div class="seat">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/carnival/about/meeting' }"></el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/carnival/about/seat' }"></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="seat-operate">
      <el-form :inline="true">
        <div class="seat-operate-right">
          <el-button type="info" size="small" @click="goToAddSeat()"></el-button>
        </div>
      </el-form>
    </div>

    <div>
      <el-table
        ref="singleTable"
        :data="tableData"
        highlight-current-row
        style="width: 100%;min-height:380px;"
      >
        <el-table-column type="index" label="" width="80"></el-table-column>
        <el-table-column property="name" label="" width="120"></el-table-column>
        <el-table-column property="count" label="" width="120"></el-table-column>
        <el-table-column property="level" label="" width="120">
          <template slot-scope="scope">
            <el-input
              size="small"
              v-model.number="scope.row.level"
              @blur="updateSeatLevel(scope.row)"
            ></el-input>
          </template>
        </el-table-column>
        <el-table-column label="" width="520">
          <template slot-scope="scope">
            <span
              class="seat-interval"
              v-for="item in scope.row.userInfoDtoList"
              :key="item.userId"
            >{{item.userName}}</span>
          </template>
        </el-table-column>
        <el-table-column property label="">
          <template slot-scope="scope">
            <div>
              <span @click="edit(scope.row)" class="seat-list-edit"></span>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page="param.current"
        :page-size="param.size"
        layout="total, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </div>

    <el-dialog
      custom-class="add-seat"
      :title="seatParam.id ? '':''"
      :visible.sync="isAddseat"
      width="40%"
    >
      <el-form ref="seatform" :rules="rules" :model="seatParam" label-width="140px">
        <!-- <el-form-item label="" prop="name">
          <el-input size="small" v-model="seatParam.name"></el-input>
        </el-form-item>-->
        <el-form-item label="" prop="name">
          <el-input size="small" v-model="seatParam.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="count">
          <el-input size="small" v-model.number="seatParam.count"></el-input>
        </el-form-item>
        <el-form-item label="" prop="members">
          <div>
            <span>{{seatParam.userList.length}}</span>
            <span>()</span>
          </div>
          <div>
            <el-tag
              v-for="(user,index) in seatParam.userList"
              @close="delLink(user,index)"
              :key="index"
              closable
              type
            >{{user.userName}}</el-tag>
          </div>
        </el-form-item>
        <el-form-item label="">
          <el-col :span="8">
            <el-select size="small" v-model="userParam.deptId">
              <el-option
                v-if="item.deptName"
                v-for="(item,index) in depts"
                :key="index"
                :value="item.deptId"
                :label="item.deptName"
              ></el-option>
            </el-select>
          </el-col>
          <el-col class="line" :span="2"></el-col>
          <el-col :span="8">
            <el-input placeholder="" v-model="userParam.name" size="small"></el-input>
          </el-col>
          <el-col class="line" :span="1"></el-col>
          <el-col :span="4">
            <el-button size="small" type="warning" @click="searchUser()"></el-button>
          </el-col>
        </el-form-item>
      </el-form>
      <el-table
        ref="userTable"
        :data="userTableData"
        highlight-current-row
        height="200"
        style="width: 100%;"
      >
        <el-table-column property="deptName" label=""></el-table-column>
        <el-table-column property="userName" label=""></el-table-column>
        <el-table-column property label="">
          <template slot-scope="scope">
            <el-button
              type="success"
              :disabled="scope.row.disabled"
              @click="selectUser(scope.row)"
              size="small"
            >{{scope.row.disabled?"":""}}</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @current-change="handCurrentUser"
        :current-page="userParam.current"
        :page-size="userParam.size"
        layout="total, prev, pager, next, jumper"
        :total="userTotal"
      ></el-pagination>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="isAddseat = false"> </el-button>
        <el-button size="small" type="primary" @click="addseat('seatform')"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import api from "@/api/api";
export default {
  data() {
    return {
      tableData: [],
      userTableData: [],
      depts: [],
      total: 0,
      pages: 0,
      userTotal: 0,
      userPages: 0,
      param: {
        current: 1,
        size: 5
      },
      userParam: {
        current: 1,
        size: 5,
        name: "",
        deptId: ""
      },
      isAddseat: false,
      seatParam: {
        name: "",
        count: "",
        userIdList: [],
        userList: [],
        id: ""
      },
      rules: {
        name: { type: "string", required: true, message: "" },
        count: {
          type: "number",
          min: 1,
          required: true,
          message: "0"
        }
      }
    };
  },
  created() {
    this.getDepts();
    this.getUserList();
    this.getseatList();
  },
  methods: {
    updateSeatLevel(item) {
      if (!/^([1-9][0-9]{0,1}|100)$/.test(item.level)) {
        this.$message.warning("1~100");
        return;
      }
      api.updateSeatLevel({ id: item.id, level: item.level }).then(res => {
        if (res.code == 200) {
          this.$message.success("");
          this.param.current = 1;
          this.getseatList();
        } else {
          this.$message.error(res.message || "");
        }
      });
    },
    delLink(user, index) {
      this.seatParam.userList.splice(index, 1);
    },
    edit(item) {
      item = JSON.parse(JSON.stringify(item));
      this.isAddseat = true;
      this.seatParam.name = item.name;
      this.seatParam.count = item.count;
      this.seatParam.userList = item.userInfoDtoList? item.userInfoDtoList: [];
      this.seatParam.id = item.id;
      this.getRepeatUserList();
    },
    getRepeatUserList() {
      this.userParam = {
        current: 1,
        size: 5,
        name: "",
        deptId: ""
      };
      this.getUserList();
    },
    getDepts() {
      api.depts().then(res => {
        if (res.code == "200") {
          this.depts = res.data;
          this.depts.unshift({ deptId: "", deptName: "" });
        }
      });
    },
    getseatList() {
      api.seatList(this.param).then(res => {
        if (res.code == 200) {
          this.tableData = res.data.records;
          this.pages = parseInt(res.data.pages);
          this.total = parseInt(res.data.total);
          this.param.size = parseInt(res.data.size);
        } else {
          this.$message.error(res.message || "");
        }
      });
    },
    searchUser() {
      this.userParam.current = 1;
      this.getUserList();
    },
    getUserList() {
      api.userList(this.userParam).then(res => {
        if (res.code == 200) {
          this.userTableData = res.data.records;
          this.userPages = parseInt(res.data.pages);
          this.userTotal = parseInt(res.data.total);
          this.userParam.size = parseInt(res.data.size);
          this.filterNotSeatedUsers(); //
        } else {
          this.$message.error(res.message || "");
        }
      });
    },
    handleCurrentChange(current) {
      this.param.current = current;
      this.getseatList();
    },
    handCurrentUser(current) {
      this.userParam.current = current;
      this.getUserList();
    },
    search() {
      this.param.current = 1;
      this.getseatList();
    },
    goToAddSeat() {
      this.isAddseat = true;
      this.seatParam = {
        name: "",
        count: "",
        userIdList: [],
        userList: [],
        id: ""
      };
      //
      this.getRepeatUserList();
    },
    selectUser(user) {
      let temp = this.seatParam.userList.filter(u => u.userId == user.userId);
      if (temp && temp.length) {
        this.$message.warning(`${user.userName} `);
      } else {
        if (this.seatParam.count > this.seatParam.userList.length) {
          this.seatParam.userList.push(user);
        } else {
          this.$message.warning("");
        }
      }
    },
    filterNotSeatedUsers() {
      this.userTableData.forEach(item => {
        if (item.seatName) {
          item.disabled = true;
        }
      });
    },
    addseat(formName) {
      let _self = this;
      this.$refs[formName].validate(valid => {
        if (valid) {
          //userId
          _self.seatParam.userIdList.length = 0;
          _self.seatParam.userList.forEach(u => {
            _self.seatParam.userIdList.push(`"${u.userId}"`);
          });
          if(_self.seatParam.userIdList.length == 0){
              this.$message.warning("1");
              return;
          }
          api.addSeat(_self.seatParam).then(res => {
            if (res.code == "200") {
              this.$message.success("");
              this.getseatList();
              this.isAddseat = false;
            } else {
              this.$message.error(res.message || "");
            }
          });
        } else {
          return false;
        }
      });
    }
  }
};
</script>

<style lang="less">
.seat {
  &-interval {
    margin-right: 10px;
  }
  &-list {
    &-manage {
      color: #fb846b;
      font-size: 14px;
      cursor: pointer;
    }

    &-edit {
      color: #7796ff;
      font-size: 14px;
      cursor: pointer;
    }
  }
  &-operate {
    .el-form {
      display: flex;
      align-items: center;
      &-item {
        width: 120px;
        margin-bottom: 10px;
        margin-top: 10px;
      }
    }
    &-right {
      padding: 15px 0;
      margin-left: auto;
    }
  }
}

.add-seat {
  .el-form {
    .el-input {
      width: 215px;
    }
    .el-form-item {
      margin-bottom: 10px;
    }
  }
  .el-table {
    .el-button {
    }
  }
  .el-col {
    .el-input {
      width: 100%;
    }
  }

  .line {
    height: 32px;
  }
}
</style>


